export default {
	data() {
		return {
			scenicId: 0,
			scenicDetail: {},
			guideList: [],
			hotelList: []
		}
	},
	onLoad: function(option) {
		this.scenicId = option.scenic_id
		this.getDetailById(option.scenic_id)
		this.getAllGuides()
		this.getAllHotels()
	},
	methods: {
		yuyueTicket() {
			var _this = this
			uni.request({
				url: this.$BASE_HOST + 'appointment/add',
				method: 'get',
				data: {
					'user_id': uni.getStorageSync('local_userinfo').id,
					'scenic_id': _this.scenicId,
					'guide_id': -1,
					'hotel_id': -1
				},
				success: function (res) {
					console.log(res.data)
					if (res.data.code === 20000) {
						uni.showToast({
							title: res.data.msg,
							duration: 2000
						});
					} else {
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							icon: 'none'
						});
					}
				}
			})
		},
		yuyueGuide(item) {
			var _this = this
			uni.request({
				url: this.$BASE_HOST + 'appointment/add',
				method: 'get',
				data: {
					'user_id': uni.getStorageSync('local_userinfo').id,
					'scenic_id': -1,
					'guide_id': item.id,
					'hotel_id': -1
				},
				success: function (res) {
					console.log(res.data)
					if (res.data.code === 20000) {
						uni.showToast({
							title: res.data.msg,
							duration: 2000
						});
					} else {
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							icon: 'none'
						});
					}
				}
			})
		},
		yuyueHotel(item) {
			var _this = this
			if (item.hotelFree === 0){
				uni.showToast({
					title: '没有空房间了那！！',
					duration: 2000,
					icon: 'none'
				});
				return
			}

			uni.request({
				url: this.$BASE_HOST + 'appointment/add',
				method: 'get',
				data: {
					'user_id': uni.getStorageSync('local_userinfo').id,
					'scenic_id': -1,
					'guide_id': -1,
					'hotel_id': item.id
				},
				success: function (res) {
					console.log(res.data)
					if (res.data.code === 20000) {
						uni.showToast({
							title: res.data.msg,
							duration: 2000
						});
					} else {
						uni.showToast({
							title: res.data.msg,
							duration: 2000,
							icon: 'none'
						});
					}
				}
			})
		},
		getAllHotels() {
			var _this = this
			uni.request({
				url: this.$BASE_HOST + 'hotel/getAllHotelOfScenic',
				method: 'get',
				data: {
					'scenic_id': _this.scenicId
				},
				success: function (res) {
					_this.hotelList = res.data.data
					_this.hotelList.forEach((item, index) => {
						item.hotelCover = _this.$BASE_HOST + item.hotelCover
					})
				}
			})
		},
		getAllGuides() {
			var _this = this
			uni.request({
				url: this.$BASE_HOST + 'guide/getAllGuideOfScenic',
				method: 'get',
				data: {
					'scenic_id': _this.scenicId
				},
				success: function (res) {
					_this.guideList = res.data.data
				}
			})
		},
		getDetailById(id){
			var _this = this
			uni.request({
				url: this.$BASE_HOST + 'scenic/getById',
				method: 'get',
				data: {
					'id': id
				},
				success: function (res) {
					_this.scenicDetail = res.data.data
					_this.scenicDetail.scenicType = _this.scenicDetail.scenicType.split(',')
				}
			})
		}
	}
}
